
.site-header {
    position: relative;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #fff;
    background: #090a0b no-repeat 50%;
    background-size: cover
}

.site-header:before {
    bottom: 0;
    background: rgba(0, 0, 0, .18)
}
.home-template .site-header:after {
    display: none
}
.site-header:after,
.site-header:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    display: block
}

.site-header:after {
    bottom: auto;
    height: 80px;
    background: linear-gradient(rgba(0, 0, 0, .1), transparent)
}

.site-header.no-cover:after,
.site-header.no-cover:before {
    display: none
}

.site-header-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 10vw 4vw;
    min-height: 200px;
    max-height: 450px;
    text-align: center
}

.site-title {
    z-index: 10;
    margin: 0;
    padding: 0;
    font-size: 3.8rem;
    font-weight: 700
}

.site-logo {
    max-height: 45px
}

.site-description {
    z-index: 10;
    margin: 0;
    padding: 5px 0;
    font-size: 2.2rem;
    font-weight: 300;
    letter-spacing: .5px;
    opacity: .8
}

@media (max-width:500px) {
    .site-title {
        font-size: 3rem
    }

    .site-description {
        font-size: 1.8rem
    }
}

.site-nav {
    position: relative;
    z-index: 300;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: start;
    align-items: flex-start;
    height: 40px;
    font-size: 1.2rem
}

.site-nav,
.site-nav-left {
    display: -ms-flexbox;
    display: flex;
    overflow-y: hidden
}

.site-nav-left {
    -ms-flex-align: center;
    align-items: center;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    margin-right: 10px;
    padding-bottom: 80px;
    letter-spacing: .4px;
    white-space: nowrap;
    -ms-overflow-scrolling: touch
}

.site-nav-logo {
    -ms-flex-negative: 0;
    flex-shrink: 0;
    display: block;
    margin-right: 24px;
    padding: 11px 0;
    color: #fff;
    font-size: 1.7rem;
    line-height: 1em;
    font-weight: 700;
    letter-spacing: -.5px
}

.site-nav-logo:hover {
    text-decoration: none
}

.site-nav-logo img {
    display: block;
    width: auto;
    height: 21px
}

.nav {
    display: -ms-flexbox;
    display: flex;
    margin: 0 0 0 -12px;
    padding: 0;
    list-style: none
}

.nav li {
    padding: 0;
    text-transform: uppercase
}

.nav li,
.nav li a {
    display: block;
    margin: 0
}

.nav li a {
    padding: 10px 12px;
    color: #fff;
    opacity: .8
}

.nav li a:hover {
    text-decoration: none;
    opacity: 1
}

.site-nav-right {
    height: 40px
}

.site-nav-right,
.social-links {
    -ms-flex-negative: 0;
    flex-shrink: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center
}

.social-links a:last-of-type {
    padding-right: 20px
}

.social-link {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    margin: 0;
    padding: 10px;
    color: #fff;
    opacity: .8
}

.social-link:hover {
    opacity: 1
}

.social-link svg {
    height: 1.8rem;
    fill: #fff
}

.social-link-fb svg {
    height: 1.5rem
}

.social-link-wb svg {
    height: 1.6rem
}

.social-link-wb svg path {
    stroke: #fff
}

.social-link-rss svg {
    height: 1.9rem
}

.subscribe-button {
    display: block;
    padding: 4px 10px;
    border: 1px solid #fff;
    color: #fff;
    font-size: 1.2rem;
    line-height: 1em;
    border-radius: 10px;
    opacity: .8
}

.subscribe-button:hover {
    text-decoration: none;
    opacity: 1
}

.rss-button {
    opacity: .8
}

.rss-button:hover {
    opacity: 1
}

.rss-button svg {
    margin-bottom: 1px;
    height: 2.1rem;
    fill: #fff
}

@media (max-width:700px) {
    .site-header {
        padding-right: 0;
        padding-left: 0
    }

    .site-nav-left {
        margin-right: 0;
        padding-left: 4vw
    }

    .site-nav-right {
        display: none
    }
}

.site-header-content .author-profile-image {
    z-index: 10;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    margin: 0 0 20px;
    width: 100px;
    height: 100px;
    box-shadow: 0 0 0 6px hsla(0, 0%, 100%, .1)
}

.site-header-content .author-bio {
    z-index: 10;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    margin: 5px 0 10px;
    max-width: 600px;
    font-size: 2rem;
    line-height: 1.3em;
    font-weight: 300;
    letter-spacing: .5px;
    opacity: .8
}

.site-header-content .author-meta {
    z-index: 10;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    margin: 0 0 10px;
    font-family: 'Open Sans', Georgia, serif;
    font-style: italic
}

.site-header-content .author-location svg {
    height: 1.9rem;
    stroke: #fff
}

.site-header-content .bull {
    display: inline-block;
    margin: 0 12px;
    opacity: .5
}

.site-header-content .social-link:first-of-type {
    padding-left: 4px
}

@media (max-width:500px) {
    .site-header-content .author-bio {
        font-size: 1.8rem;
        line-height: 1.15em;
        letter-spacing: 0
    }

    .author-location,
    .author-stats {
        display: none
    }
}

.error-template .site-main {
    padding: 7vw 4vw
}

.site-nav-center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center
}

.site-nav-center .site-nav-logo {
    margin-right: 0
}