.progress {
    position: absolute;
    right: 0;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 2px;
    border: none;
    color: #3eb0ef;
    background: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none
}

.progress::-webkit-progress-bar {
    background-color: transparent
}

.progress::-webkit-progress-value {
    background-color: #3eb0ef
}

.progress::-moz-progress-bar {
    background-color: #3eb0ef
}

.progress-container {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: transparent
}

.progress-bar {
    display: block;
    width: 50%;
    height: inherit;
    background-color: #3eb0ef
}