.site-footer {
    position: relative;
    padding-top: 20px;
    padding-bottom: 60px;
    color: #fff;
    background: #000
}

.site-footer-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
    font-size: 1.3rem
}

.site-footer-content,
.site-footer-content a {
    color: hsla(0, 0%, 100%, .7)
}

.site-footer-content a:hover {
    color: #fff;
    text-decoration: none
}

.site-footer-nav {
    display: -ms-flexbox;
    display: flex
}

.site-footer-nav a {
    position: relative;
    margin-left: 20px
}

.site-footer-nav a:before {
    content: "";
    position: absolute;
    top: 11px;
    left: -11px;
    display: block;
    width: 2px;
    height: 2px;
    background: #fff;
    border-radius: 100%
}

.site-footer-nav a:first-of-type:before {
    display: none
}

@media (max-width:650px) {
    .site-footer-content {
        -ms-flex-direction: column;
        flex-direction: column
    }

    .site-footer-nav a:first-child {
        margin-left: 0
    }
}
