.post-full-footer {
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
    margin: 0 auto;
    padding: 3vw 0 6vw;
    max-width: 840px
}

.author-card,
.post-full-footer {
    display: -ms-flexbox;
    display: flex
}

.author-card .author-profile-image,
.author-card .avatar-wrapper {
    width: 60px;
    height: 60px
}

.author-card-name {
    margin: 8px 0 2px;
    padding: 0;
    font-size: 2rem
}

.author-card-name a {
    color: #15171a;
    font-weight: 700
}

.author-card-name a:hover {
    text-decoration: none
}

.author-card-content p {
    margin: 0;
    color: #738a94;
    line-height: 1.3em
}

.post-full-footer-right {
    -ms-flex-negative: 0;
    flex-shrink: 0;
    margin-left: 20px
}

.author-card-button {
    display: block;
    padding: 9px 16px;
    border: 1px solid #aebbc1;
    color: #738a94;
    font-size: 1.2rem;
    line-height: 1;
    font-weight: 500;
    border-radius: 20px;
    transition: all .2s ease
}

.author-card-button:hover {
    border-color: #3eb0ef;
    color: #3eb0ef;
    text-decoration: none
}

.post-full-authors {
    -ms-flex-positive: 1;
    flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    margin-top: 20px;
    padding-top: 40px;
    border-top: 1px solid #e3e9ed
}

.post-full-authors-content {
    margin-bottom: 20px
}

.post-full-authors-content p {
    margin-bottom: 0;
    color: #738a94;
    font-size: 1.4rem;
    letter-spacing: .2px;
    text-align: center;
    text-transform: uppercase
}

.post-full-authors-content a {
    display: inline-block;
    color: #424852;
    font-size: 1.4rem;
    font-weight: 600;
    text-transform: uppercase
}

.post-full-footer .author-list {
    -ms-flex-pack: center;
    justify-content: center;
    padding: 10px 20px
}

.author-card .author-profile-image,
.author-card .avatar-wrapper {
    position: relative;
    margin-right: 15px
}

.author-list-item .author-card {
    position: absolute;
    bottom: 130%;
    left: 50%;
    z-index: 300;
    display: block;
    margin-left: -160px;
    width: 320px;
    font-size: 1.4rem;
    letter-spacing: .2px;
    background: #fff;
    border-radius: 6px;
    box-shadow: 0 12px 26px rgba(39, 44, 49, .08), 1px 3px 8px rgba(39, 44, 49, .03);
    opacity: 0;
    transition: all .3s cubic-bezier(.4, .01, .165, .99);
    transform: scale(.98) translateY(15px);
    pointer-events: none
}

.author-list-item .author-card:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    margin-left: -12px;
    width: 0;
    height: 0;
    border-top: 12px solid #fff;
    border-right: 12px solid transparent;
    border-left: 12px solid transparent
}

.author-list-item .author-card.hovered {
    opacity: 1;
    transform: scale(1) translateY(0);
    pointer-events: auto
}

.author-card .basic-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    padding: 30px 20px 20px;
    color: #fff;
    background: #15171a;
    border-radius: 6px 6px 0 0
}

.author-card .basic-info h2 {
    margin: 1em 0 .5em
}

.author-card .bio {
    padding: 20px 20px 0
}

@media (max-width:650px) {
    .author-list-item .author-card {
        display: none
    }
}